import { React, useState, useEffect } from 'react'
import GameForm from './GameForm'
import axios from '../config/axios'

import { Container, Table } from 'react-bootstrap'

const Games = () => {
  const [games, setGames] = useState([])

  useEffect(() => {
    axios.get('/api/games')
      .then(games => setGames(games.data))
  }, [])


  const addGame = (game) => {
    axios.post('/api/games', game)
      .then(() => {
        axios.get('/api/games')
          .then(games => setGames(games.data))
      })
  }

  const renderedGames = games.map(g => {
    return(
      <tr key={g.gameID}>
        <td>{g.team1}</td>
        <td>{g.team2}</td>
        <td>{g.gameLocation}</td>
        <td>{g.gameDate}</td>
        <td>{g.gameTime}</td>
        <td>{g.isPlayoff ? "Yes" : "No"}</td>
      </tr>
    )
  })

  return (
    <Container>
      <GameForm addGame={addGame} />
      <h1>Games</h1>
      <Table>
        <thead>
          <tr>
            <th>Team 1</th>
            <th>Team 2</th>
            <th>Location</th>
            <th>Date</th>
            <th>Time</th>
            <th>Playoff?</th>            
          </tr>
        </thead>
        <tbody>
          {renderedGames}
        </tbody>
      </Table>
    </Container>
  )
}

export default Games

