import { React, useState, useEffect } from 'react'
import axios from '../config/axios'
import TeamForm from './TeamForm'

import {
  Link
} from 'react-router-dom'

import { Table, Container, Button } from 'react-bootstrap'

const Teams = () => {
  const [teams, setTeams] = useState([])
  const [leagues, setLeagues] = useState([])

  useEffect(() => {
    axios.get('/api/teams')
      .then(teams => setTeams(teams.data))
  }, [])

  useEffect(() => {
    axios.get('/api/leagues')
      .then(leagues => setLeagues(leagues.data))
  }, [])


  const addTeam = (e, team) => {
    e.preventDefault()
    axios.post('/api/teams', team)
      .then(() => {
        axios.get('/api/teams')
          .then(teams => setTeams(teams.data))
      })
  }

  const removeFromLeague = (e, id) => {
    e.preventDefault()
    const team = teams.find(t => +t.id === +id)
    const teamNoLeague = {...team, teamName: team.name, leagueID: null}
    axios.put(`/api/teams/${id}`, teamNoLeague)
      .then(() => {
        axios.get('/api/teams') 
          .then(teams => setTeams(teams.data))
      })
  }

  const renderedTeams = teams.map(t => {
    return (
      <tr key={t.id}>
        <td>
          <Link to={`/rosters/${t.id}`}>
            {t.name}
          </Link>
        </td>
        <td>{t.wins}</td>
        <td>{t.losses}</td>
        <td>{t.leagueName || "Not in a League"}</td>
        <td>{t.leagueName ?
        <Button variant="warning" onClick={(e) => removeFromLeague(e, t.id)}>Remove from league</Button>
        : null
        }
        </td>
      </tr>
    )
  })

  return (
    <Container>
      <h1>Add a Team</h1>
        <TeamForm addTeam={addTeam} leagues={leagues} />
      <h1>Teams</h1>
      <h3 style={{ color: 'red' }}><u>Click a team name to view roster</u></h3>
      <Table>
        <thead>
          <tr>
            <th>Name</th>
            <th>Wins</th>
            <th>Losses</th>
            <th>League</th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          {renderedTeams}
        </tbody>
      </Table>
    </Container>
  )
}

export default Teams