import { React, useState } from 'react'

import { Form, Row, Col, Button } from 'react-bootstrap'

const TeamForm = ({ leagues, addTeam }) => {
  const [name, setName] = useState('')
  const [league, setLeague] = useState('')
  const [wins, setWins] = useState(0)
  const [losses, setLosses] = useState(0)
  const [linkToLeague, setLinkToLeague] = useState(false)


  const leagueOptions = leagues.map(l => {
    return (
      <option value={l.id} key={l.id}>
        {l.leagueName} - {l.level} {l.gender} {l.sport} from {l.startDate} to {l.endDate}
      </option>
    )
  })

  const team = {leagueID: league || null, teamName: name, wins, losses}

  const handleLinkToLeague = () => {
    setLinkToLeague(!linkToLeague)
  }

  return (
    <div>
      <Form onSubmit={(e) => addTeam(e, team)}>
        <Form.Group as={Row}>
          <Form.Label column sm="2">Team Name:</Form.Label>
          <Col sm="10">
            <Form.Control type="text" value={name} onChange={(e) => setName(e.target.value)} required />
          </Col>
        </Form.Group>
        {linkToLeague ? 
        <Form.Group as={Row}>
          <Form.Label column sm="2">League:</Form.Label>
          <Col sm="10">
            <Form.Control as="select" value={league} onChange={(e) => setLeague(e.target.value)}>
              <option value="" disabled hidden>Choose League</option>
              {leagueOptions}
            </Form.Control>
          </Col>
        </Form.Group>
        : null
        }
        <Form.Group as={Row}>
          <Col sm="10">
            <Form.Check type="checkbox" label="Link Team to League?" checked={linkToLeague} onChange={() => handleLinkToLeague()} />
          </Col>
        </Form.Group>
        <Form.Row>
          <Form.Group as={Col}>
            <Form.Label>Wins:</Form.Label>
            <Form.Control type="number" value={wins} onChange={(e) => setWins(e.target.value)} required />
          </Form.Group>
          <Form.Group as={Col}>
            <Form.Label>Losses:</Form.Label>
            <Form.Control type="number" value={losses} onChange={(e) => setLosses(e.target.value)} required />
          </Form.Group>
        </Form.Row>
        <Button type="submit">Add Team</Button>
      </Form>
    </div>
  )
}

export default TeamForm