import React, { useState, useEffect } from 'react'
import axios from '../config/axios'
import { useParams } from 'react-router-dom'

import { Form, Button, Container, Row, Col, ListGroup } from 'react-bootstrap'

const Roster = () => {
  const [team, setTeam] = useState(null)
  const [teamPlayers, setTeamPlayers] = useState([])
  const [playersToAdd, setPlayersToAdd] = useState([])
  const [playerSelected, setPlayerSelected] = useState("")
  const id = useParams().id

  useEffect(() => {
    axios.get('/api/teams')
      .then(team => setTeam(team.data.find(t => t.id === +id)))
  }, [id])

  useEffect(() => {
    axios.get(`/api/rosters/team/${id}`)
      .then(response => setTeamPlayers(response.data))
  }, [id])

  useEffect(() => {
    axios.get('/api/players')
      .then(response => {
        setPlayersToAdd(response.data)
      })
  }, [])

  const addPlayer = async (e) => {
    e.preventDefault()
    await axios.post('/api/rosters', { playerID: playerSelected, teamID: team.id })
    const newPlayers = await axios.get(`/api/rosters/team/${team.id}`)
    setPlayerSelected("")
    setTeamPlayers(newPlayers.data)
  }

  const handleDelete = (e, id) => {
    e.preventDefault()
    axios.delete(`/api/rosters/${id}`)
      .then(() => {
        axios.get(`/api/rosters/team/${team.id}`)
          .then(response => setTeamPlayers(response.data))
      })
  }

  const playerOptions = playersToAdd.map(p => {
    if (teamPlayers.find(tp => tp.playerID === p.playerID)){
      return null
    }
    return (
      <option value={p.playerID} key={p.playerID}>
        {p.firstName} {p.lastName}
      </option>
    )
  }).filter(p => p)

  const renderedPlayers = teamPlayers.map(p => {
    return (
      <ListGroup.Item key={p.playerID}>
        {p.playerName}
        <Button variant="danger" onClick={(e) => handleDelete(e, p.playerTeamID)}>Delete</Button>
      </ListGroup.Item>
    )
  })

  const allPlayerAdded = playerOptions.length === 0
  

  if (!team) {
    return null
  }

  return (
    <Container>
      <h1>{team.name} Roster</h1>
      <h3>Add Players</h3>
      {allPlayerAdded 
      ? "All players are on this team" : 
      <Form onSubmit={(e) => addPlayer(e)}>
        <Form.Group as={Row}>
          <Form.Label column sm="2">Player:</Form.Label>
          <Col sm="10">
          <Form.Control as="select" value={playerSelected} onChange={(e) => setPlayerSelected(e.target.value)}>
              <option value="" disabled hidden>Choose Player</option>
              {playerOptions}
          </Form.Control>
          </Col>
        </Form.Group>
        <Button type="submit">Add player to team</Button>
      </Form>
      }
      <h3>Players</h3>
      <ul>
        {renderedPlayers}
      </ul>
    </Container>
  )
}

export default Roster