import { React, useEffect, useState } from 'react'
import axios from '../config/axios'
import {
  Link
} from 'react-router-dom'
import { Table, Button, Container, Form, Row, Col } from 'react-bootstrap'


import PlayerForm from './PlayerForm'

const Players = () => {
  const [players, setPlayers] = useState([])
  const [playerSearch, setPlayerSearch] = useState('')

  useEffect(() => {
    axios.get('/api/players')
      .then(players => setPlayers(players.data))
  }, [])

  const addPlayer = (player) => {
    axios.post('/api/players', player)
      .then(() => {
        axios.get('/api/players')
          .then(players => setPlayers(players.data))
      })
  }

  const deletePlayer = (id) => {
    axios.delete('/api/players/' + id)
      .then(() => {
        axios.get('/api/players')
          .then(players => setPlayers(players.data))
      })
  }

  const searchForPlayer = (e) => {
    e.preventDefault()
    axios.get('/api/players/name', { params: { playerName: playerSearch }})
      .then(players => setPlayers(players.data))
  }

  const renderedPlayers = (
    <div>
      <Table striped bordered>
        <thead>
          <tr>
            <th>Name</th>
            <th>Gender</th>
            <th>Age</th>
            <th>Email</th>
            <th>Phone #</th>
            <th></th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          {players.map(p => {
            return (
              <tr key={p.playerID}>
                <td>
                  {`${p.firstName} ${p.lastName}`}
                </td>
                <td>{p.gender}</td>
                <td>{p.age}</td>
                <td>{p.email}</td>
                <td>{p.phone || "No Phone #"}</td>
                <td>
                  <Button variant="danger" onClick={() => deletePlayer(p.playerID)}>Delete</Button>
                </td>
                <td>
                  <Button as={Link} to ={`/player/${p.playerID}`} variant="warning">Edit</Button>
                </td>     
              </tr>
            )
          })}
        </tbody>
      </Table>
    </div>
  )

  return (
    <Container>
      <h1>Add a Player</h1>
        <PlayerForm addPlayer={addPlayer} />
      <h1>Search for Player</h1>
      <Form onSubmit={(e) => searchForPlayer(e)}>
        <Form.Group as={Row}>
          <Form.Label column sm="2">Player Name:</Form.Label>
          <Col sm="10">
            <Form.Control type="text" value={playerSearch} onChange={(e) => setPlayerSearch(e.target.value)} />
          </Col>
        </Form.Group>
        <Button type="submit">Search</Button>
      </Form>
      <h1>Players</h1>
      <h4>Click a name to edit</h4>
        {renderedPlayers}
    </Container>
  )
}

export default Players