import { React, useState } from 'react'

import { Form, Container, Button, Row, Col } from 'react-bootstrap'

const LeagueForm = ({ addLeague }) => {
  const [sport, setSport] = useState('Basketball')
  const [gender, setGender] = useState('Co-Ed')
  const [level, setLevel] = useState('Recreational')
  const [startDate, setStartDate] = useState('')
  const [endDate, setEndDate] = useState('')
  const [leagueName, setLeagueName] = useState('')

  const league = {sport, gender, level, startDate: startDate || null, endDate: endDate || null, leagueName}

  return (
    <Container>
      <Form onSubmit={(e) => (e.preventDefault(), addLeague(league))}>
        <Form.Group as={Row}>
          <Form.Label column sm="2">League Name:</Form.Label>
          <Col sm="10">
            <Form.Control type="text" value={leagueName} onChange={(e) => setLeagueName(e.target.value)} required/>
          </Col>
        </Form.Group>
        <Form.Group as={Row}>
          <Form.Label column sm="2">Sport:</Form.Label>
          <Col sm="10">
            <Form.Control as="select" value={sport} onChange={(e) => setSport(e.target.value)}>
              <option value="Basketball">Basketball</option>
              <option value="Soccer">Soccer</option>
              <option value="Beach Volleyball">Beach Volleyball</option>
              <option value="Indoor Volleyball">Indoor Volleyball</option>
              <option value="Softball">Softball</option>
              <option value="Kickball">Kickball</option>
              <option value="Hockey">Hockey</option>
              <option value="Lacrosse">Lacrosse</option>
              <option value="Flag Football">Flag Football</option>
              <option value="Running">Running</option>
            </Form.Control>
          </Col>
        </Form.Group>
        <Form.Group as={Row}>
        <Form.Label column sm="2">Gender:</Form.Label>
        <Col sm="10">
          <Form.Control as="select" value={gender} onChange={(e) => setGender(e.target.value)}>
            <option value="Co-Ed">Co-Ed</option>
            <option value="Male">Male</option>
            <option value="Female">Female</option>
          </Form.Control>
        </Col>
        </Form.Group>
        <Form.Group as={Row}>
          <Form.Label column sm="2">Level:</Form.Label>
          <Col sm="10">
            <Form.Control as="select" value={level} onChange={(e) => setLevel(e.target.value)}>
              <option value="Recreational">Recreational</option>
              <option value="Intermediate">Intermediate</option>
              <option value="Competitive">Competitive</option>
            </Form.Control>
          </Col>
        </Form.Group>
        <Form.Group as={Row}>
          <Form.Label column sm="2">Start Date:</Form.Label>
          <Col sm="10">
            <Form.Control type="date" value={startDate} onChange={(e) => setStartDate(e.target.value)} />
          </Col>
        </Form.Group>
        <Form.Group as={Row}>
          <Form.Label column sm="2">End Date:</Form.Label>
          <Col sm="10">
            <Form.Control type="date" value={endDate} onChange={(e) => setEndDate(e.target.value)} />
          </Col>
        </Form.Group>
        <Button type="submit">Add League</Button>
      </Form>
    </Container>
  )
}

export default LeagueForm