import Players from './components/Players'
import Leagues from './components/Leagues'
import Teams from './components/Teams'
import Games from './components/Games'
import Roster from './components/Roster'
import Player from './components/Player'

import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link,
  Redirect
} from 'react-router-dom'

import { Container, Nav } from 'react-bootstrap'

import 'bootstrap/dist/css/bootstrap.min.css'

const App = () => {
  return (
      <Router>
        <Container>
          <Nav variant="tabs" defaultActiveKey="/players">
            <Nav.Item>
              <Nav.Link as={Link} to="/players" eventKey="/players">
                Players
              </Nav.Link>
            </Nav.Item>
            <Nav.Item>
              <Nav.Link as={Link} to="/teams" eventKey="/teams">
                Teams/Rosters
              </Nav.Link>
            </Nav.Item>
            <Nav.Item>
              <Nav.Link as={Link} to="/leagues" eventKey="/leagues">
                  Leagues
              </Nav.Link>
            </Nav.Item>
            <Nav.Item>
              <Nav.Link as={Link} to="/games" eventKey="/games">
                  Games
              </Nav.Link>
            </Nav.Item>
          </Nav>
        </Container>
        <Switch>
            <Route path="/teams">
              <Teams />
            </Route>
            <Route path="/leagues">
              <Leagues />
            </Route>
            <Route path="/player/:id">
              <Player />
            </Route>
            <Route path="/players">
              <Players />
            </Route>
            <Route path="/games">
              <Games />
            </Route>
            <Route path="/rosters/:id">
              <Roster />
            </Route>
            <Route path="/">
              <Redirect to="/players" />
            </Route>
        </Switch>
      </Router>
  )
}

export default App