import { React, useState, useEffect } from 'react'
import axios from '../config/axios'
import LeagueForm from './LeagueForm'

import { Container, Table } from 'react-bootstrap'

const Leagues = () => {
  const [leagues, setLeagues] = useState([])

  useEffect(() => {
    axios.get('/api/leagues')
      .then(leagues => setLeagues(leagues.data))
  }, [])

  const createLeague = (league) => {
    axios.post('/api/leagues', league)
      .then(() => {
        axios.get('/api/leagues')
          .then(leagues => setLeagues(leagues.data))
      })
    return
  }

  const renderedLeagues = (
    <Container>
      <Table striped bordered>
        <thead>
          <tr>
            <th>Name</th>
            <th>Level</th>
            <th>Gender</th>
            <th>Sport</th>
            <th>Start Date</th>
            <th>End Date</th>
          </tr>
        </thead>
        <tbody>
          {leagues.map(l => {
            return (
              <tr key={l.id}>
                <td>{l.leagueName}</td>
                <td>{l.level}</td>
                <td>{l.gender}</td>
                <td>{l.sport}</td>
                <td>{l.startDate}</td>
                <td>{l.endDate}</td>
              </tr>
            )
          })}
        </tbody>
      </Table>
    </Container>
  )

  return (
    <Container>
      <h1>Add a League</h1>
        <LeagueForm addLeague={createLeague} />
      <h1>Leagues</h1>
        {renderedLeagues}
    </Container>
  )
}

export default Leagues