import { React, useState, useEffect } from 'react'
import axios from '../config/axios'

import { Form, Row, Col, Button, Container } from 'react-bootstrap'

const GameForm = ({ addGame }) => {
  const [team1ID, setTeam1ID] = useState('')
  const [team2ID, setTeam2ID] = useState('')
  const [leagueID, setLeague] = useState('')
  const [location, setLocation] = useState('')
  const [date, setDate] = useState('')
  const [time, setTime] = useState('')
  const [playoff, setPlayoff] = useState(false)

  const [teams, setTeams] = useState([])
  const [leagues, setLeagues] = useState([])

  const [teamsInLeague, setTeamsInLeague] = useState([])

  const game = {leagueID: leagueID, team1ID, team2ID, gameLocation: location, gameDate: date, gameTime: time, isPlayoff: playoff}

  useEffect(() => {
    axios.get('/api/teams')
      .then(teams => setTeams(teams.data))
  }, [])

  useEffect(() => {
    axios.get('/api/leagues')
      .then(leagues => setLeagues(leagues.data))
  }, [])

  useEffect(() => {
    setTeamsInLeague(teams.filter(t => {
      return +t.leagueID === +leagueID
    }))
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [leagueID])

  const handlePlayoffToggle = () => {
    setPlayoff(!playoff)
  }

  const renderedTeam1Options = teamsInLeague.map(team => {
    return (
      <option value={team.id} key={team.id}>{team.name}</option>
    )
  })

  const renderedTeam2Options = teamsInLeague.map(team => {
    if (+team.id === +team1ID) {
      return null
    } else {
      return (
        <option value={team.id} key={team.id}>{team.name}</option>
      )
    }
  })

  const leagueOptions = leagues.map(l => {
    return (
      <option value={l.id} key={l.id}>
        {l.leagueName} {l.gender} {l.level} {l.sport}
      </option>
    )
  })

  const noTeamWarning = () => {
    if (leagueID && teamsInLeague.length === 0) {
      return (
        "No teams in this league, choose another!!"
      )
    } else {
      return null
    }
  }

  return (
    <Container>
      <h1>Add a Game</h1>
      <Form onSubmit={(e) => (e.preventDefault(), addGame(game))}>
        <Form.Group as={Row}>
          <Form.Label column sm="2">League:</Form.Label>
          <Col sm="10">
            <Form.Control as="select" value={leagueID} onChange={(e) => setLeague(e.target.value)}>
              <option value="" disabled hidden>Choose League</option>
              {leagueOptions}
            </Form.Control>
          </Col>
        </Form.Group>
        <h1>
          {noTeamWarning()}
        </h1>
        <Form.Row>
          <Form.Group as={Col}>
            <Form.Label>Team 1:</Form.Label>
            <Form.Control as="select" value={team1ID} onChange={(e) => setTeam1ID(e.target.value)}>
              <option value="" disabled hidden>Choose Team</option>
              {renderedTeam1Options}
            </Form.Control>
          </Form.Group>
          <Form.Group as={Col}>
            <Form.Label>Team 2:</Form.Label>
            <Form.Control as="select" value={team2ID} onChange={(e) => setTeam2ID(e.target.value)}>
              <option value="" disabled hidden>Choose Team</option>
              {renderedTeam2Options}
            </Form.Control>
          </Form.Group>
        </Form.Row>
        <Form.Group as={Row}>
          <Form.Label column sm="2">Location:</Form.Label>
          <Col sm="10">
            <Form.Control type="text" value={location} onChange={(e) => setLocation(e.target.value)} required />
          </Col>
        </Form.Group>
        <Form.Group as={Row}>
          <Form.Label column sm="2">Date:</Form.Label>
          <Col sm="10">
            <Form.Control type="date" value={date} onChange={(e) => setDate(e.target.value)} required />
          </Col>
        </Form.Group>
        <Form.Group as={Row}>
          <Form.Label column sm="2">Time:</Form.Label>
          <Col sm="10">
            <Form.Control type="time" value={time} onChange={(e) => setTime(e.target.value)} required />
          </Col>
        </Form.Group>
        <Form.Group as={Row}>
          <Col sm="10">
            <Form.Check type="checkbox" label="Playoff game" checked={playoff} onChange={() => handlePlayoffToggle()} />
          </Col>
        </Form.Group>
        <Button type="submit">Add Game</Button>
      </Form>
    </Container>
  )
}

export default GameForm