import axios from '../config/axios'
import { React, useEffect, useState } from 'react'
import { Redirect } from 'react-router-dom'
import { Form, Row, Col, Button, Container } from 'react-bootstrap'

const PlayerForm = ({ addPlayer, playerToEdit }) => {
  const [firstName, setFirstName] = useState('')
  const [lastName, setLastName] = useState('')
  const [gender, setGender] = useState('male')
  const [birthday, setBirthday] = useState('')
  const [email, setEmail] = useState('')
  const [phone, setPhone] = useState('')

  const [redirect, setRedirect] = useState(false)

  useEffect(() => {
    if (playerToEdit) {
      setFirstName(playerToEdit.firstName)
      setLastName(playerToEdit.lastName)
      setGender(playerToEdit.gender)
      setBirthday(playerToEdit.birthday)
      setEmail(playerToEdit.email)
      setPhone(playerToEdit.phone || "")
    }
  }, [playerToEdit])

  const player = {firstName, lastName, gender, birthday, email, phone}

  const editPlayer = (id, player) => {
    axios.put(`/api/players/${id}`, player)
      .then(response => {
        setRedirect(true)
      })
  }

  const handleSubmit = (e) => {
    e.preventDefault()
    if (addPlayer) {
      addPlayer(player)
    } else {
      editPlayer(playerToEdit.playerID, player)
    }
  }

  return (
    <Container>
      {redirect ? <Redirect to="/players" /> : null}
      <Form onSubmit={(e) => handleSubmit(e)}>
        <Form.Group as={Row}>
          <Form.Label column sm="2">First Name:</Form.Label>
          <Col sm="10">
            <Form.Control type="text" value={firstName} onChange={(e) => setFirstName(e.target.value)} required />
          </Col>
        </Form.Group>
        <Form.Group as={Row}>
          <Form.Label column sm="2">Last Name:</Form.Label>
          <Col sm="10">
            <Form.Control type="text" value={lastName} onChange={(e) => setLastName(e.target.value)} required />
          </Col>
        </Form.Group>
        <Form.Group as={Row}>
          <Form.Label column sm="2">Gender:</Form.Label>
          <Col sm="10">
            <Form.Control as="select" value={gender} onChange={(e) => setGender(e.target.value)} required>
              <option value="Male">Male</option>
              <option value="Female">Female</option>
            </Form.Control>
          </Col>
        </Form.Group>
        <Form.Group as={Row}>
          <Form.Label column sm="2">Birthday:</Form.Label>
          <Col sm="10">
            <Form.Control type="date" value={birthday} onChange={(e) => setBirthday(e.target.value)} required />
          </Col>
        </Form.Group>
        <Form.Group as={Row}>
          <Form.Label column sm="2">Email:</Form.Label>
          <Col sm="10">
            <Form.Control type="email" value={email} onChange={(e) => setEmail(e.target.value)} required />
          </Col>
        </Form.Group>
        <Form.Group as={Row}>
          <Form.Label column sm="2">Phone #:</Form.Label>
          <Col sm="10">
            <Form.Control type="tel" value={phone} onChange={(e) => setPhone(e.target.value)} />
          </Col>
        </Form.Group>
        <Button type="submit">{playerToEdit ? "Edit Player" : "Add Player"}</Button>
      </Form>
    </Container>
  )
}

export default PlayerForm